:root {
  --blue: #0af;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  border-bottom: 1px solid #e5e5e5;
  width: 100%;
}

.container {
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
}

.logo img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  margin-left: 243px;
}

.nav-list {
  list-style: none;
  display: flex;
}

.nav-list li:not(:last-child) {
  margin-right: 24px;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-link:hover {
  color: var(--blue);
}

.nav-link:after {
  content: "";
  background-color: var(--blue);
  width: 0;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-link.is-active {
  color: var(--blue);
}

.nav-link:hover:after, .nav-link.is-active:after {
  width: 100%;
}
